html{
}

body{
    margin: 0;
}

.people {
    display: grid;
    grid-template-columns: 1fr; 
    gap: 20px; 
    text-align: center; 
  }
  
  .person {
    display: flex;
    flex-direction: column;
    align-items: center;

    cursor: pointer;
    position: relative;

    & person:hover .extra {
        visibility: visible;
      }
  }

  
  .photo {
    position: relative;

    & img {
        width: 200px; 
        height: 231px;
        border-radius: 50%; 
        object-fit: cover;
      }
  }
    
  .rank {
    position: absolute;
    top: 50%;
    right: 5px;
    padding: 5px 10px;
    
    color: black;
    font-weight: bold;
    border-radius: 50%;
    background: rgb(255, 255, 255);
  }
  
  .details {
    margin-top: 10px; 
  }
  
  .name {
    display: block;
    margin-top: 5px;
    
    font-size: 18px;
    font-weight: bold;
  }
  
  .extra {
    opacity: 0;
    visibility: hidden;
    transition: opacity 0.3s ease visibility 0.3s ease;
    font-size: 14px;
    color: rgb(0, 0, 0);
  }
  
  @media (min-width: 500px) {
    .people {
      grid-template-columns: repeat(2, 1fr);
    }
  }
  
  @media (min-width: 1000px) {
    .people {
      grid-template-columns: repeat(4, 1fr); 
    }
  }

